      }],
      itemNum:0,
      win:'',
      win2:'',
      colors:['rgba(209,52,164,1.0)','rgba(209,52,164,1.0)','rgba(209,52,164,1.0)'],
      gradient:["rgb(255,255,255)", "rgb(249,232,244)", "rgb(244,209,234)", "rgb(239,187,224)", "rgb(234,164,214)", "rgb(229,142,204)", "rgb(224,119,194)", "rgb(219,97,184)", "rgb(214,74,174)"],
