  mounted() {
    let this_ = this;
    //
    this.initData();
    this.win = $(window).width()>$('#myChart').height()?$('#myChart').height():$(window).width();
     console.log(this.win,'win')
    if(this.win<125){
       console.log('xiao;e')
      this.win = 238
    }
    this.myChart = echarts.init(document.getElementById('myChart'));
