              rotate: 'radial',
              fontWeight: '100'
            },
            downplay: {
              label: {
                opacity: 0.5
              }
            }
          }
        ],
        nodeClick: false,
        data: this.data
      }
    };

    this.myChart.setOption(option);

    this.starSize();
      //
    let thisa_ = this
    setTimeout(function () {
      thisa_.win = thisa_.win2
      thisa_.myChart.resize()
