  methods: {
    test(){
      this.myChart.resize()
      console.log('211211pppppp')
    },
    starSize(){
      if(this.itemNum>30){
        return 6;
      }else{
        return 8;
      }
    },
    initData(){
      let that_ = this
  for (var i = 0; i < this.data.length; i++) {
    //:
    this.data[i].itemStyle = {
      color: this.colors[i]
    }
    var level1 = this.data[i].children;
    for (var j = 0; j < level1.length; j++) {
      //opacity:
      var opacity = parseInt(level1[j].itemStyle.opacity);
      var val = opacity;
      var name = level1[j].name;
      level1[j].name = name.substr(0,2)+'\n'+name.substr(2);
      level1[j].children = [{
        name: val,
        children: [{
          name: '',
          itemStyle: {
            color: that_.gradient[opacity-1]
          },
          children: level1[j].children
        }]
      }];
      level1[j].children.forEach(function(blook){
        var block = blook.children[0].children;
        for (var star = 0; star < block.length; star++) {
          //:
          var style = (function (name) {
            switch (name) {
              case '':
                // bookScoreId = 3;
                return that_.gradient[8];
              case '':
                // bookScoreId = 2;
                return that_.gradient[5];
              case '':
                // bookScoreId = 1;
                return that_.gradient[2];
              case '':
                // bookScoreId = 0;
                return that_.gradient[0];
            }
          })(block[star].name);

          block[star].label = {
            color: style,
          };
          that_.itemNum++;
          block[star].itemStyle = {
            shadowColor: that_.gradient[opacity-1]
          }
          block[star].value = 1;
          var starChild = block[star].children;
          block[star].children = [{
            name: '',
            value: 1,
            itemStyle: {
              color: that_.gradient[opacity-1]
            },
            label: {
              color: that_.gradient[opacity-1]
            },
            children: starChild
          }]
          block[star].children.forEach(function(book){
            book.value = 1;
            book.itemStyle = {
              color: that_.gradient[opacity-1]
            };
            book.label = {
              color: that_.gradient[opacity-1]
            }
            book.children.forEach(function(txt){
              txt.value = 1;
              var tName = txt.name;
              var tNameL = tName.length%2==0?tName.length/2:tName.length/2+1;
              txt.name = tName.substr(0,tNameL)+'\n'+tName.substr(tNameL);
              txt.label = {
                color: '#FFFFFF'
              }
            })
          })
        }
      })
    }

  }
}
  },
  watch: {
