let echarts = require('echarts/lib/echarts')
export default {
  name: "bar-chart",
  props: ["name", "name2"],
  data() {
    return {
      myChart: {},
      xName :[],
      xData:[[],[],[]],
      data:[
        {
          name: '', //
          value: 2.8,		//
          referValue: 3.5,//
          percent: false	//90
        },
        {
          name: '',
          value: 3.2,
          referValue: 2.4,
          percent: false
        },
        {
          name: '',
          value: 4.3,
          referValue: 3.5,
          percent: false
        },
        {
          name: '',
          value: 5.5,
          referValue: 2.2,
          percent: true
        },
        {
          name: '',
          value: 1.8,
          referValue: 3.8,
          percent: false
        },
        {
          name: '',
          value: 2.2,
          referValue: 2.8,
          percent: false
        },
        {
          name: '',
          value: 2.8,
          referValue: 3.5,
          percent: false
        },
        {
          name: '',
          value: 1.9,
          referValue: 3.7,
          percent: false
        },
        {
          name: '',
          value: 2.8,
          referValue: 1.5,
          percent: false
        },
        {
          name: '',
          value: 4.3,
          referValue: 3.5,
          percent: false
        },
        {
          name: '',
          value: 3.1,
          referValue: 2.4,
          percent: false
        }
      ]
    }
  },
  mounted() {
    let this_ = this;
    //
    this.dataFormatter();
    this.myChart = echarts.init(document.getElementById('myChart'));
    let option = {
      backgroundColor: '',
      title: {
        text: '181212',
        left: 15,
        top: 9,
        textStyle: {
          color: '#fff',
          fontSize: '8',
          fontWeight: '100'
        },
      },
      grid: {
        borderWidth: 0,
        top: 40,
        bottom: 40,
        left: 20,
        right: 10,
        textStyle: {
          color: '#fff'
        }
      },
      legend: {
        top: 9,
        right: 5,
        itemWidth: 6,
        itemHeight: 7,
        padding: [5, 0, 0, 0],
        itemGap: 0,
        textStyle: {
          color: '#ffffff',
          fontSize: 8,
          fontWeight: 100
        },
        data: ['', '', '90']
      },
      xAxis: [{
        type: 'category',
        axisLine: {
          show: false
        },
        splitLine: {
          show: true,
          lineStyle: {
            color: 'rgba(255,255,255,0.03)'
          }
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#FFF', // 
            fontWeight: '100',
            fontSize: '9'
          },
          formatter: function (p) {
            var tNameL = p.length % 2 == 0 ? p.length / 2 : p.length / 2 + 1;
            return p.substr(0, tNameL) + '\n' + p.substr(tNameL)
          }
        },
        data: this.xName,
      }],
      yAxis: [{
        type: 'value',
        min: 0,
        max: 9,
        splitNumber: 9,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisLabel: {
          show: true,
          formatter: function (p) {
            if (p == 0) {
              $('#ablity-v').html('<p class="ablity-b" style=height:calc(100%/9)><span>V' + (9 - p) + '</span></p>')
            } else {
              if (p == 9) return;
              $('#ablity-v').append('<p class="ablity-b" style="height:calc(100%/9);border-top:1px solid rgba(255,255,255,0.1)"><span>V' + (9 - p) + '</span></p>')
            }
          }
        },
        splitArea: {
          show: false,
          areaStyle: {
            color: ['rgba(213,71,172,0.2)']
          }
        }
      }, {
        name: '',
        type: 'value',
        min: 0,
        max: 9,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisLabel: {
          show: false
        }
      }, {
        name: '',
        type: 'value',
        min: 0,
        max: 9,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisLabel: {
          show: false
        }
      }],
      dataZoom: [{
        type: 'inside',
        show: false,
        startValue: 0,
        endValue: 8
      }],
      series: [{
        name: '',
        type: 'bar',
        stack: '',
        barWidth: 20,
        itemStyle: {
          normal: {
            color: 'rgba(255,255,255,0.6)',
            barBorderRadius: [4, 4, 0, 0],
            label: {
              show: true,
              textStyle: {
                color: '#fff',
                fontWeight: '100',
                fontSize: '10'
              },
              position: 'top',

              formatter: function (p) {
                return p.value > 0 ? 'V' + (parseInt(p.value)) : '';
              }
            }
          }
        },
        data: this.xData[0]
      }, {
        name: '',
        yAxisIndex: 1,
        type: 'line',
        showAllSymbol: true,
        symbol: 'image://data:image/png;base64,',
        symbolSize: 8,
        data: this.xData[1],
        itemStyle: {
          opacity: 0.8
        },
        lineStyle: {
          color: 'rgba(74,212,179,1)',
          width: 1,
          type: 'dashed',
          opacity: 0.7
        }
      }, {
        name: '90',
        yAxisIndex: 1,
        type: 'line',
        showAllSymbol: true,
        symbol: 'image://data:image/png;base64,',
        symbolSize: 10,
        data: this.xData[2],
        itemStyle: {
          opacity: 1
        },
        lineStyle: {
          width: 0
        }
      }]
    };
    this.myChart.setOption(option);
    let thisa_ = this
    setTimeout(function () {
      thisa_.myChart.resize()
    },500)
  },
  methods: {
    test() {
      console.log('diaxxxxxxxxxx')
      this.myChart.resize()
    },

    dataFormatter() {
      for (var i = 0; i < this.data.length; i++) {
        this.xName.push(this.data[i].name);
        this.xData[0].push(this.data[i].value);
        this. xData[1].push(this.data[i].referValue);
        if (this.data[i].percent) {
          // var h = $('#print-tupu-canvas').height();
          let h = this.$refs.myChart.offsetHeight;
          // console.log(h,'xxxxxxxxxxxxxx')
          this.xData[2].push(this.data[i].value - 100 / h);
        } else {
          this. xData[2].push('');
        }
      }
    }
  },
  watch: {
    name: function () {
      console.log('bar name')
      let that = this
      setTimeout(function () {
        that.myChart.resize()
      }, 500)
    },
    // name2: function (eeee, cccc) {
    //   console.log('XXXXXXXXXXX')
    // }
  },
}



