
import BarChart from '@/components/demo/BarChart';
import PieChart from '@/components/demo/PieChart';
import RadarChart from '@/components/demo/RadarChart';
import InfoBar from '@/components/demo/InfoBar';
import InfoPie from '@/components/demo/InfoPie';
import InfoRadar from '@/components/demo/InfoRadar';
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
name: "ocean",
data() {
return {
tableData: [],
cardount: '',
currentView: 'InfoBar', // 
eChartsView: 'BarChart',
active: 0,
move: 0,
eCheight: 60, //echarts 
show: true,
show2: false,
name: 'bbbb',
name2:'cccc', // margine
smHight:0,
curHeight: 0,// 
objeChar:'',
objiFor:'',
objJob:'',
moveS:'no',//
tabs: [
{
type: '',
view: 'InfoBar',
view2: 'BarChart',
class1: 'icon-zhengjian'
},
{
type: '',
view: 'InfoPie',
view2: 'PieChart',
class1: 'icon-dengpao1'
},
{
type: '',
view: 'InfoRadar',
view2: 'RadarChart',
class1: 'icon-zhiyeshengyamubiao'
}
]
}
},
components: {
BarChart,  //
PieChart,  //
RadarChart, //
InfoBar,
InfoPie,
InfoRadar,
Scroller
},
mounted() {
console.log(window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,'====-=-----')
this.getcurHeight()
this.getdata()

this.objeChar = document.getElementById('eCharts')
this.objiFor = document.getElementById('xx1')
this.$nextTick(()=>{
if(document.querySelector('.xxx3')){
console.log('zheshi sha ?')
}
})
},
methods: {


getdata() {
this.$axios.get('http://192.168.1.230/api/Job/special').then((response) => {
this.tableData = response.data.data
this.cardount = (this.tableData.length) * 250 + (this.tableData.length) * 10 + (10)  
console.log(this.tableData)
}).catch((response) => {
console.log(response)
})
},
/**
* 
*/
getcurHeight() {
let h = document.documentElement.clientHeight || document.body.clientHeight;
let objeChar = document.getElementById('eCharts')
let objiFor = document.getElementById('xx1')
this.curHeight = h;
if(this.curHeight<=568){
this.smHight = 1
objeChar.style.height='50vh'
objiFor.style.height = 'calc(50vh - 42px)'
}
},
/**
* 
*/

setHeight(curHeight) {
console.log(curHeight,'=========')
},
tabChange(i, tab1, tab2) {
this.active = i
this.currentView = tab1;
this.eChartsView = tab2;
if(this.smHight === 1){ //iphone5
this.objeChar.style.height='50vh';
this.objiFor.style.height = 'calc(50vh - 42px)'
}
this.move = 0;
this.show2 = false;
this.name = 'bbbb';
this.name2 = 'cccc'
},
swiperup: function () {
this.moveS = 'yes'
if(this.smHight === 1){
this.objeChar.style.height='30vh';
this.objiFor.style.height = 'calc(40vh - 42px)';
setTimeout(function () {
let xx = document.getElementById('xx3')
this.objJob = document.getElementById('xx3')
this.objJob.style.height = '30vh'
},50)
}
this.move = 1;
this.show2 = true;
this.name = 'ffff';
this.name2 = 'eeee';
console.log(this.name2)
},
},
watch: {
moveS: function (yes, no) {


},
},
}




